
/* eslint-disable indent,camelcase,prefer-promise-reject-errors */

import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Picker from 'vux/src/components/picker/index.vue'

import UtilMixin from '@/mixins/UtilMixin.vue'
import FormMixin from '@/mixins/FormMixin.vue'

export default {
mixins: [UtilMixin, FormMixin],
data() {
return {
formList: [],
groupColor: '#50b97b',
reqId: null,
partnerId: 'yousu',
smsCode: null,
cityList: [],
cityCode: null,
data: null,
baseInfo: JSON.parse(this.$route.query.baseInfo)
}
},
components: {
XInput,
Group,
XButton,
Box,
XAddress,
Picker
},
computed: {
picker() {
const data = this.data || []
let filterData = []
data.map(item => {
if (item.status === '0') {
filterData.push(item)
}
})
return filterData
}
},
methods: {
formMixin_submit(ajaxUrl) {

const ISEMPTY = 0
const ISNOVALID = 1
return new Promise((resolve, reject) => {
let params = Object.assign(this.baseInfo)
let extraData = {}
for (let key in this.$refs) {
let ref = null
if (Array.isArray(this.$refs[key])) {
if (this.$refs[key].length !== 0) {
ref = this.$refs[key][0]
} else {
break
}
} else {
ref = this.$refs[key]
}
if (ref.required && !ref.currentValue) {
this.$vux.toast.text('请将信息填写完整', 'top')
ref.focus()
reject({
code: ISEMPTY,
msg: '信息填写不完整'
})
return
} else {
if (!ref.valid) {
this.$vux.toast.text(ref.firstError, 'top')
ref.focus()
reject({
code: ISNOVALID,
msg: '信息填写不完整'
})
return
}
}
if (ref.name) {
extraData[ref.name] = ref.currentValue
}
params['extraData'] = extraData
params['reqId'] = this.reqId
params['partnerId'] = this.partnerId
}
this.$vux.loading.show({
text: '等待中...'
})
this.submitLoadding = true
params = {
url: 'https://credit.baiqishi.com/clweb/api/hfund/login',
data: params
}
console.log(params)
this.$axios._post({
url: ajaxUrl,
data: params,
tips: true
}).then(result => {
this.$vux.loading.hide()
this.submitLoadding = false
this.$vux.toast.show({
text: '认证成功'
})
resolve(result)
}).catch(err => {
this.submitLoadding = false
this.$vux.loading.hide()
if (err.code === 'CCOM3069') {
this.reqId = err.data.reqId
this.$vux.toast.show({
text: '验证码已发送'
})
resolve(err.data.reqId)
} else {
this.$vux.toast.show({
type: 'cancel',
text: err.msg
})
reject({
code: err.code,
msg: err.msg
})
}
})
})
},
submit(type) {
this.formMixin_submit('/bqs_api/')
.then((res) => {
console.log(res)
if (type === 1) {
this.$router.push({name: 'SelectAuth', query: {checkway: this.$route.query.checkway}})
}
})
.catch((err) => {
console.log(`code:${err.code} \n msg:${err.msg}`)
})
},
ajaxLoginType() {
this.$axios._post({
url: '/bqs_api/',
data: {
url: 'https://credit.baiqishi.com/clweb/api/hfund/logintype',
data: {
partnerId: this.partnerId,
cityCode: this.baseInfo.cityCode
}
}
}).then(res => {
this.formList = res.loginCmdFields[0].loginFields
})
}
},
created() {
console.log(this.baseInfo)
this.ajaxLoginType()
},
mounted() {
}
}



